<template>
    <div class="search-data">
        <div class="search van-hairline--bottom">
            <div class="back" @click="$routerBack">
                <div class="ico"></div>
            </div>
            <div class="input" @click="$routerBack">
                <div class="keyword">{{ $route.query.keyword }}</div>
            </div>
            <div class="text">
                搜索
            </div>
        </div>
        <div class="data">
            <div class="empty" v-if="video.empty">
                搜索不到相关内容
            </div>
            <div class="box" v-if="!video.empty">

                <van-list v-model="video.loading" :finished="video.finished" finished-text="没有更多了" @load="initData" :immediate-check="false">
                    <div class="item" v-for="(item,index) in video.data" :key="index" @click="videoHandel(item.uuid)">
                        <div class="image">
                            <img :src="item.thumbnail">
                        </div>
                        <div class="info">
                            <div class="title">{{ item.title }}</div>
                            <div class="tags">
                                <div class="tag" v-if="key<3" v-for="(value,key) in item.tags" :key="key">
                                    {{ value }}
                                </div>
                            </div>
                            <div class="star">
                                <div class="ico">
                                    <van-icon name="star" color="#FFEA03"/>
                                </div>
                                <div class="text">{{ item.star }}</div>
                            </div>
                        </div>
                    </div>
                </van-list>

            </div>
        </div>
    </div>
</template>

<script>
    import {Icon, List} from 'vant';

    export default {
        components: {
            [Icon.name]: Icon,
            [List.name]: List
        },
        data() {
            return {
                search: {
                    value: ''
                },
                video: {
                    data: [],
                    empty: false,
                    loading: false,
                    finished: false
                },
                page: {
                    video: 1
                }
            }
        },
        beforeRouteEnter(to, from, next) {
            to.meta.Load = false;
            if (from.path !== '/video/index' || to.meta.Create !== true) {
                to.meta.Load = true;
            }
            next();
        },
        beforeRouteLeave(to, from, next) {
            //标识页面已经加载过
            from.meta.Create = true;
            if (to.path !== '/video/index') {
                this.video = {
                    data: [],
                    empty: false,
                    loading: false,
                    finished: false
                };
                this.page = {
                    video: 1
                };
            }
            next();
        },
        activated() {
            if (this.$route.meta.Load) {
                this.initData();
            }
        },
        mounted() {

        },
        methods: {
            initData() {
                this.video.loading = true;
                this.$axios.get('/api/app/search/data', {
                    page: this.page.video,
                    keyword: this.$route.query.keyword
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.video.loading = false;
                            if (result.data.length === 0) {
                                this.video.finished = true;
                            }
                            this.page.video++;
                            this.video.data = this.video.data.concat(result.data);
                            if (this.video.data.length === 0) {
                                this.video.empty = true;
                            }
                        }
                    })
            },
            videoHandel(uuid) {
                this.$router.push('/video/index?uuid=' + uuid)
            }
        }
    }
</script>

